import styled from "styled-components";
export const SearchDetailWrapper = styled.div`
  position: relative;
  display: flex;
  width: 55%;
  box-sizing: border-box;
  height: 66px;
  border: 1px solid #e0e0e0;
  border-radius: 35px;
  cursor: pointer;
  .detailsection {
    flex: 1;
    border-radius: 35px;
    &:hover {
      background-color: #e0e0e0;
    }
    :last-child {
      .innersection {
        border-right: none;
      }
    }
    .innersection {
      border-right: 1px solid #e0e0e0;
      margin: 15px 8px 15px 20px;
      .title {
        font-weight: 600;
        font-size: 10px;
      }
    }
  }
  .searchIcon {
    position: absolute;
    right: 4px;
    top: 4px;
    bottom: 4px;
    color: #fff;
    background-color: ${(props) => props.theme.color.first};
    padding: 20px;
    border-radius: 50%;
    &:hover {
      background-color: #d52b49;
    }
  }
`;
