import useScrollPosition from "@/hooks/useScrollPosition";
import React, { memo, useState, useRef } from "react";
import { useSelector } from "react-redux";
import HeaderCenter from "./child-comps/header-center";
import HeaderLeft from "./child-comps/header-left";
import HeaderRight from "./child-comps/header-right";
import { HeaderWrapper } from "./style";

const AppHeader = memo(() => {
  const { isFixed } = useSelector((state) => state.main.headerConfig);
  const [showSearch, setShowSearch] = useState(false);
  const scrollY = useScrollPosition();
  const currentY = useRef(0);
  if (!showSearch) currentY.current = scrollY;
  if (showSearch && Math.abs(scrollY - currentY.current) > 50) {
    setShowSearch(false);
  }
  return (
    <HeaderWrapper className={isFixed ? "fixed" : null} showSearch={showSearch}>
      <div className="content">
        <div className="top">
          <HeaderLeft />
          <HeaderCenter showSearch={showSearch} setShowSearch={setShowSearch} />
          <HeaderRight />
        </div>
        <div className="search-area"></div>
      </div>
      {showSearch && (
        <div className="cover" onClick={(e) => setShowSearch(false)}></div>
      )}
    </HeaderWrapper>
  );
});

export default AppHeader;
