import * as actionTypes from "./constants";
const initialState = {
  currentPage: 1,
  totalCount: 0,
  roomsList: [],
  isLoading: false,
};
function reducer(state = initialState, action) {
  switch (action.type) {
    case actionTypes.CHANGE_ENTIRE_PAGE:
      return { ...state, currentPage: action.page };
    case actionTypes.CHANGE_ENTIRE_TOTAL_AMOUNT:
      return { ...state, totalCount: action.totalAmount };
    case actionTypes.CHANGE_ENTIRE_ROOMSLIST:
      return { ...state, roomsList: action.roomList };
    case actionTypes.CHANGE_ENTIRE_ISLOADING:
      return { ...state, isLoading: action.isLoading };
    default:
      return state;
  }
}
export default reducer;
