import styled from "styled-components";
export const SearchTabWrapper = styled.div`
  .titles {
    display: flex;
    .active {
      background-color: #222222;
      height: 2px;
      position: absolute;
      bottom: -10px;
      width: 64px;
    }
    .title-item {
      font-size: 16px;
      color: #222222;
      font-weight: 500;
      margin: 0 15px;
      cursor: pointer;
      position: relative;
      &::after {
        content: "";
        background-color: #222222;
        height: 2px;
        position: absolute;
        bottom: -10px;
        left: 50%;
        width: 0;
        transition: all 400ms ease;
      }
      &:hover {
        opacity: 0.6;
        &::after {
          content: "";
          left: 0;
          width: 64px;
        }
      }
    }
  }
`;
