import IconSearchBar from "@/assets/svg/icon-search-bar";
import React, { memo, useState } from "react";
import { CSSTransition } from "react-transition-group";
import { CenterWrapper } from "./style";
import searchInfos from "@/assets/data/search_titles.json";
import Title from "./childrens/search-tab";
import SearchDetail from "./childrens/search_detail";
const HeaderCenter = memo((props) => {
  const { showSearch, setShowSearch } = props;
  const titles = searchInfos.map((item) => item.title);
  const [currentTab, setCurrentTab] = useState(0);
  return (
    <CenterWrapper>
      <CSSTransition
        in={!showSearch}
        classNames="bar"
        timeout={150}
        unmountOnExit={true}
      >
        <div className="sereach-bar" onClick={(e) => setShowSearch(true)}>
          <div className="text">
            <span>搜索房源和体验</span>
          </div>
          <div className="icon">
            <IconSearchBar />
          </div>
        </div>
      </CSSTransition>
      <CSSTransition
        in={showSearch}
        classNames="detail"
        timeout={150}
        unmountOnExit={true}
      >
        <div className="searchdetail">
          <Title
            titleInfo={titles}
            currentTab={currentTab}
            setCurrentTab={setCurrentTab}
          />
          <div className="info">
            <SearchDetail
              setShowSearch={setShowSearch}
              searchInfo={searchInfos[currentTab].searchInfos}
            />
          </div>
        </div>
      </CSSTransition>
    </CenterWrapper>
  );
});

export default HeaderCenter;
