import IconAvatar from '@/assets/svg/icon_avatar'
import IconGlobal from '@/assets/svg/icon_global'
import IconMenu from '@/assets/svg/icon_menu'
import React, { memo, useEffect, useState } from 'react'
import { RightWrapper } from './style'

const HeaderRight = memo(() => {
  const [showPannel,setShowPannel] = useState({style:null,isShow:false})
  function iconPannelClick(e){
    setShowPannel({style:{boxShadow:`0 2px 4px rgba(0, 0, 0, 0.18)`,},isShow:true
  })
  }
  useEffect(()=>{
    function windowAddListener(){
      setShowPannel({style:null,isShow:false})
    }
    window.addEventListener("click",windowAddListener,true)
    return ()=>{
      window.removeEventListener("click",windowAddListener,true)
    }
  },[])
  return (
    <RightWrapper>
      <div className='btns'>
      <span className='button'>登录</span>        
      <span className='button'>注册</span>        
      <span className='button'><IconGlobal/></span>        
      </div>
      <div className='profile' onClick={iconPannelClick} >
        <div className='iconPannel' style={showPannel.style} >
        <IconMenu/>
        <IconAvatar/>
        </div>
        {(showPannel.isShow)&&(<div className='loginPannel'>
          <div className='top'>
            <div className='item registered'>注册</div>
            <div className='item'>登录</div>
          </div>
          <div className="bottom">
            <div className='item'>来爱彼迎发布房源</div>
            <div className='item'>发布体验</div>
            <div className='item'>帮助</div>
          </div>
        </div>)}
      </div>
    </RightWrapper>
  )
})

export default HeaderRight