import styled from "styled-components";
export const RightWrapper = styled.div`
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  .btns {
    display: flex;
    margin-right: 10px;
    .button {
      padding: 14px 15px 14px;
      cursor: pointer;
      border-radius: 24px;
      color: ${(props) => props.theme.text.frist};
      font-weight: 550;
      font-size: 14px;
      &:hover {
        background-color: #f7f7f7;
      }
    }
  }
  .profile {
    width: 80px;
    position: relative;
    .iconPannel {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 5px 0 15px;
      height: 40px;
      border: solid 1px #e0e0e0;
      border-radius: 24px;
      cursor: pointer;
      ${(props) => props.theme.mixin.boxShadow}
      color: ${(props) => props.theme.text.second};
    }
    .loginPannel {
      width: 240px;
      background-color: #fff;
      position: absolute;
      top: 50px;
      right: 0;
      border-radius: 12px;
      ${(props) => props.theme.mixin.shadow}
      .top,.bottom {
        padding: 12px 0;
        .item {
          height: 40px;
          padding: 0 15px;
          cursor: pointer;
          &:hover {
            background-color: #eee;
          }
          line-height: 40px;
        }
      }
      .top {
        border-bottom: 1px solid #e0e0e0;
      }
    }
  }
`;
