import styled from "styled-components";
export const CenterWrapper = styled.div`
  position: relative;
  justify-content: center;
  display: flex;
  height: 40px;
  .sereach-bar {
    position: absolute;
    width: 256px;
    height: 48px;
    border: 1px solid #eee;
    border-radius: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 8px 0 15px;
    cursor: pointer;
    ${(props) => props.theme.mixin.shadow}
    ${(props) => props.theme.mixin.boxShadow}
    .text {
      flex: 1;
      font-weight: bold;
    }
    .icon {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      background-color: ${(props) => props.theme.color.first};
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
    }
  }
  //展开组件

  .info {
    position: absolute;
    top: 80px;
    bottom: 20px;
    left: -500px;
    right: -500px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  //动画
  .detail-exit {
    transform: scale(1) translateY(0);
    opacity: 1;
  }
  .detail-exit-active {
    transition: all 150ms ease;
    transform: scale(0.35, 0.7) translateY(-58px);
  }
  .detail-enter {
    transform: scale(0.35, 0.7) translateY(-58px);
    opacity: 0;
  }
  .detail-enter-active {
    transform: scale(1) translateY(0);
    opacity: 1;
  }
  .bar-exit {
    opacity: 0;
  }
  .bar-exit-active {
    transition: all 150ms ease;
  }
`;
