import { createSlice } from "@reduxjs/toolkit";
const detailsSlice = createSlice({
  name: "detail",
  initialState: {
    roomDetailInfo: [],
  },
  reducers: {
    changeRoomDetailInfo(state, { payload }) {
      state.roomDetailInfo = payload;
    },
  },
});
export const { changeRoomDetailInfo } = detailsSlice.actions;
export default detailsSlice.reducer;
