import styled from "styled-components";
export const HeaderWrapper = styled.div`
  border-bottom: 1px solid #eee;
  .content {
    position: relative;
    background-color: #fff;
    z-index: 99;
    .top {
      display: flex;
      align-items: center;
      height: 80px;
      padding: 0 24px;
    }
    .search-area {
      height: ${(props) => (props.showSearch ? "80px" : 0)};
      transition: height 250ms ease;
    }
  }
  .cover {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 19;
  }
  &.fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background-color: #fff;
    z-index: 999;
  }
`;
